@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  height: 74px; }

.header__wrapper {
  display: flex;
  align-items: center;
  height: 100%; }

.header__logo {
  margin-left: 35px; }

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  padding-left: 112px;
  padding-right: 112px;
  min-height: 540px;
  opacity: 1;
  transition: opacity 1s ease-out; }
  @media (max-width: 1023px) {
    .preloader {
      padding-left: 109px;
      padding-right: 109px; } }
  .preloader--close {
    opacity: 0;
    --webkit-animation: preloader-close 3s 1s ease-in-out infinite;
    animation: preloader-close 3s 1s ease-in-out infinite; }
    .preloader--close .preloader__logo {
      --webkit-animation: preloader-logo-close 1s ease-out 1;
      animation: preloader-logo-close 1s ease-out 1;
      opacity: 0; }

.preloader__logo {
  --webkit-animation: preloader-logo 1s ease-out 1;
  animation: preloader-logo 1s ease-out 1;
  height: auto;
  width: 100%;
  max-width: 1024px;
  margin: auto; }

.preloader__text {
  position: absolute;
  bottom: 100px;
  left: 0;
  width: 100%;
  text-align: center;
  font-weight: normal;
  font-size: 24px;
  line-height: 22px;
  --webkit-animation: preloader-text 1s ease-in-out infinite;
  animation: preloader-text 1s ease-in-out infinite; }

.preloader--touch .preloader__text--enter {
  visibility: hidden; }

.preloader--no-touch .preloader__text--touch {
  visibility: hidden; }

@keyframes preloader-text {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.6; }
  100% {
    opacity: 1; } }

@keyframes preloader-logo {
  0% {
    transform: translate(-150%, 0); }
  100% {
    transform: translate(0, 0); } }

@keyframes preloader-logo-close {
  0% {
    transform: translate(0, 0);
    opacity: 1; }
  100% {
    transform: translate(150%, 0);
    opacity: 0; } }

@keyframes preloader-close {
  0% {
    width: 0;
    height: 0;
    padding: 0; }
  100% {
    width: 0;
    height: 0;
    padding: 0; } }

.content {
  padding-left: 35px;
  padding-right: 35px;
  max-width: 1024px;
  margin: auto;
  background-image: url("../img/svg/BG-A.svg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: 664px;
  margin-top: 40px;
  min-height: 651px;
  height: auto; }

.content__wrapper {
  display: flex; }

.content__text p {
  margin-top: 64px;
  font-size: 96px;
  line-height: 96px;
  color: #2c39f2; }

.content__aside {
  display: flex;
  align-items: center; }
  .content__aside p {
    margin-top: 25px;
    margin-bottom: 90px;
    font-size: 30px;
    line-height: 40px;
    width: 352px; }

.content__image {
  max-width: 316px; }

@media (max-width: 1023px) {
  .content__aside {
    color: #ffffff;
    background-color: #2c39f2;
    position: fixed;
    top: 71px;
    left: 0;
    width: 100vw;
    height: calc(100vh - 71px);
    transform: translate(100%, 0);
    transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1); }
    .content__aside--active {
      transform: translate(0, 0); }
  .content__aside-wrapper {
    margin: auto; }
  .content__text p {
    margin: 130px auto auto 35px;
    max-width: 799px; }
  .content {
    margin: 0;
    padding: 0;
    background-image: none; }
  .lightning {
    fill: #ffffff; }
  .flag {
    fill: #ffffff;
    stroke: #000000; } }

.content--active .content__image {
  --webkit-animation: image 1s ease-out 1;
  animation: image 1s ease-out 1; }

.content--active .lightning {
  --webkit-animation: lightning-op  2s ease-out 1, lightning-up  3s ease-out 1;
  animation: lightning-op  2s ease-out 1, lightning-up  3s ease-out 1; }

.content--active .content__aside p {
  --webkit-animation: text-right 1s ease-out 1;
  animation: text-right 1s ease-out 1; }

.content--active .content__text p {
  --webkit-animation: text-left 1s ease-out 1;
  animation: text-left 1s ease-out 1; }

@keyframes image {
  0% {
    transform: translate(150%, 0);
    opacity: 0; }
  100% {
    transform: translate(0, 0);
    opacity: 1; } }

@keyframes text-right {
  0% {
    transform: translate(0, 150%);
    opacity: 0; }
  100% {
    transform: translate(0, 0);
    opacity: 1; } }

@keyframes text-left {
  0% {
    transform: translate(-150%, 0);
    opacity: 0; }
  100% {
    transform: translate(0, 0);
    opacity: 1; } }

@keyframes lightning-up {
  0% {
    transform: translate(0, 30px); }
  100% {
    transform: translate(0, 0); } }

@keyframes lightning-op {
  0% {
    opacity: 0; }
  51% {
    opacity: 0; }
  100% {
    opacity: 1; } }

